<template>
  <mdb-container>
    <mdb-row>
      <mdb-col style="margin-bottom: 1em;" v-for="(project, i) in projects" :key="i" :md="project.col">

        <div @click="openModal(project)">
          <mdb-card style="background-color: rgba(4, 24, 39, 0.85);">
            <mdb-card-image
              v-if="project.img.png"
              class="project"
              :src="require(`@/assets/${project.img.thumb}`)"
              :alt="project.description"
            ></mdb-card-image>
            <img v-else style="height: 20vh; margin: 1em;" :src="require(`@/assets/${project.img.local}`)" alt>
            <mdb-card-body style="background: #000000b3; margin-top: 1em;">
              <mdb-card-title style="font-size: 1.7rem; color: #67e61f;">{{project.title}}</mdb-card-title>
              <mdb-card-text>{{project.description}}</mdb-card-text>
              <a v-if="project.projectURL" :href="project.projectURL" target="_blank">
                <mdb-btn color="primary">{{`Visit ${project.title}`}}</mdb-btn>
              </a>
            </mdb-card-body>
          </mdb-card>
        </div>
      </mdb-col>
    </mdb-row>

    <mdb-modal
      v-if="projectModal.project"
      size="fluid"
      :show="projectModal.visible"
      @close="projectModal.visible = false"
    >
      <mdb-modal-header style="color: #041019; font-size: 2em;">
        <mdb-modal-title v-if="projectModal.project.title">{{projectModal.project.title}}</mdb-modal-title>
      </mdb-modal-header>
      <mdb-modal-body>
        <img
          style="height: 90vh;"
          v-if="projectModal.project.img"
          :src="projectModal.project.img.png"
          class="img-fluid"
          alt="Responsive image"
        >
      </mdb-modal-body>
      <mdb-modal-footer style="  justify-content: center;">
        <mdb-btn color="secondary" size="sm" @click.native="projectModal.visible = false">Close</mdb-btn>
      </mdb-modal-footer>
    </mdb-modal>
  </mdb-container>
</template>


<script>
import {
  mdbContainer,
  mdbRow,
  mdbCol,
  mdbCard,
  mdbCardImage,
  mdbCardBody,
  mdbCardTitle,
  mdbCardText,
  mdbBtn,
  mdbModal,
  mdbModalHeader,
  mdbModalTitle,
  mdbModalBody,
  mdbModalFooter
} from "mdbvue";
export default {
  name: "ProjectsContainer",
  props: ["projects"],
  components: {
    mdbContainer,
    mdbRow,
    mdbCol,
    mdbCard,
    mdbCardImage,
    mdbCardBody,
    mdbCardTitle,
    mdbCardText,
    mdbBtn,
    mdbModal,
    mdbModalHeader,
    mdbModalTitle,
    mdbModalBody,
    mdbModalFooter
  },
  data() {
    return {
      projectModal: {
        project: {},
        visible: false
      }
    };
  },
  methods: {
    goToProject(projectURL) {
      this.$router.push(projectURL);
    },
    openModal(project) {
      if (!project || !project.img.png) {
        return;
      }
      console.log(project.title);
      this.projectModal = {
        visible: true,
        project
      };
    }
  }
};
</script>

<style>
.project img {
  height: 22vh;
  margin-top: 1em;
}
.card .card-body .card-text {
  color: #1b6cab;
  font-size: 1rem;
  font-weight: 400;
}
.btn-primary {
  background-color: #071a29 !important;
  color: #fff;
}
.modal-body {
  background: #041019;
}
</style>
